"use client";

import { useEffect, useState } from "react";
import { useRouter } from "next/navigation";
import DashboardLayout from "@/components/dashboard-layout";
import { Button } from "@/components/ui/button";
import {
  Card,
  CardContent,
  CardDescription,
  CardHeader,
  CardTitle,
} from "@/components/ui/card";
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from "@/components/ui/table";
import { Badge } from "@/components/ui/badge";
import { 
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select";
import { Calendar, Clock, Scissors } from "lucide-react";

// Dados simulados para agendamentos
const mockAppointments = [
  {
    id: 1,
    barber: "João Silva",
    service: "Corte & Barba",
    date: "2023-06-15",
    time: "10:00",
    status: "concluído",
    price: 35,
  },
  {
    id: 2,
    barber: "Pedro Santos",
    service: "Corte de Cabelo",
    date: "2023-06-20",
    time: "14:30",
    status: "confirmado",
    price: 25,
  },
  {
    id: 3,
    barber: "Carlos Lima",
    service: "Aparar Barba",
    date: "2023-06-25",
    time: "11:00",
    status: "pendente",
    price: 15,
  },
  {
    id: 4,
    barber: "João Silva",
    service: "Barbear com Toalha Quente",
    date: "2023-05-10",
    time: "15:00",
    status: "concluído",
    price: 20,
  },
  {
    id: 5,
    barber: "Pedro Santos",
    service: "Coloração",
    date: "2023-05-15",
    time: "13:00",
    status: "concluído",
    price: 50,
  },
  {
    id: 6,
    barber: "Carlos Lima",
    service: "Corte de Cabelo",
    date: "2023-05-20",
    time: "09:30",
    status: "cancelado",
    price: 25,
  },
];

export default function CustomerAppointmentsPage() {
  const router = useRouter();
  const [role, setRole] = useState<"admin" | "barber" | "customer">("customer");
  const [filter, setFilter] = useState("all");
  const [appointments, setAppointments] = useState(mockAppointments);

  useEffect(() => {
    const userRole = localStorage.getItem("userRole") as
      | "admin"
      | "barber"
      | "customer"
      | null;
    if (!userRole) {
      router.push("/login");
      return;
    }
    setRole(userRole);
    if (userRole !== "customer") {
      router.push(`/${userRole}/dashboard`);
    }
  }, [router]);

  useEffect(() => {
    if (filter === "all") {
      setAppointments(mockAppointments);
    } else {
      setAppointments(mockAppointments.filter(apt => apt.status === filter));
    }
  }, [filter]);

  const handleCancelAppointment = (id: number) => {
    setAppointments(appointments.map(apt => 
      apt.id === id ? { ...apt, status: "cancelado" } : apt
    ));
    mockAppointments.find(apt => apt.id === id)!.status = "cancelado";
    alert("Agendamento cancelado com sucesso!");
  };

  const handleRescheduleAppointment = (id: number) => {
    router.push("/customer/book");
  };

  return (
    <DashboardLayout role="customer">
      <div className="space-y-6">
        <div className="flex items-center justify-between">
          <div>
            <h1 className="text-3xl font-bold">Meus Agendamentos</h1>
            <p className="text-gray-400">
              Visualize e gerencie seu histórico de agendamentos
            </p>
          </div>
          <Button 
            className="bg-amber-600 hover:bg-amber-700 text-black"
            onClick={() => router.push("/customer/book")}
          >
            Agendar Novo Horário
          </Button>
        </div>

        <Card className="bg-gray-900 border-gray-800">
          <CardHeader>
            <div className="flex items-center justify-between">
              <div>
                <CardTitle>Histórico de Agendamentos</CardTitle>
                <CardDescription>
                  Todos os seus agendamentos passados e futuros
                </CardDescription>
              </div>
              <div className="w-40">
                <Select value={filter} onValueChange={setFilter}>
                  <SelectTrigger className="bg-gray-800 border-gray-700">
                    <SelectValue placeholder="Filtrar por status" />
                  </SelectTrigger>
                  <SelectContent>
                    <SelectItem value="all">Todos os Agendamentos</SelectItem>
                    <SelectItem value="confirmed">Confirmados</SelectItem>
                    <SelectItem value="pending">Pendentes</SelectItem>
                    <SelectItem value="completed">Concluídos</SelectItem>
                    <SelectItem value="cancelled">Cancelados</SelectItem>
                  </SelectContent>
                </Select>
              </div>
            </div>
          </CardHeader>
          <CardContent>
            <Table>
              <TableHeader>
                <TableRow>
                  <TableHead>Barbeiro</TableHead>
                  <TableHead>Serviço</TableHead>
                  <TableHead>Data e Hora</TableHead>
                  <TableHead>Preço</TableHead>
                  <TableHead>Status</TableHead>
                  <TableHead className="text-right">Ações</TableHead>
                </TableRow>
              </TableHeader>
              <TableBody>
                {appointments.map((appointment) => (
                  <TableRow key={appointment.id}>
                    <TableCell className="font-medium">
                      {appointment.barber}
                    </TableCell>
                    <TableCell>{appointment.service}</TableCell>
                    <TableCell>
                      {appointment.date} às {appointment.time}
                    </TableCell>
                    <TableCell>R$ {appointment.price}</TableCell>
                    <TableCell>
                      <Badge
                        variant={
                          appointment.status === "concluído"
                            ? "default"
                            : appointment.status === "confirmado"
                            ? "secondary"
                            : appointment.status === "pendente"
                            ? "outline"
                            : "destructive"
                        }
                        className={
                          appointment.status === "concluído"
                            ? "bg-green-600"
                            : appointment.status === "confirmado"
                            ? "bg-amber-600"
                            : appointment.status === "pendente"
                            ? "bg-blue-600"
                            : appointment.status === "cancelado"
                            ? "bg-red-600"
                            : ""
                        }
                      >
                        {appointment.status}
                      </Badge>
                    </TableCell>
                    <TableCell className="text-right">
                      {appointment.status === "confirmado" || appointment.status === "pendente" ? (
                        <>
                          <Button
                            variant="outline"
                            size="sm"
                            className="mr-2 border-gray-700 hover:bg-gray-700"
                            onClick={() => handleRescheduleAppointment(appointment.id)}
                          >
                            Reagendar
                          </Button>
                          <Button
                            variant="outline"
                            size="sm"
                            className="border-red-500 text-red-500 hover:bg-red-500/10"
                            onClick={() => handleCancelAppointment(appointment.id)}
                          >
                            Cancelar
                          </Button>
                        </>
                      ) : (
                        <Button
                          variant="outline"
                          size="sm"
                          className="border-gray-700 hover:bg-gray-700"
                          onClick={() => router.push("/customer/book")}
                        >
                          Agendar Novamente
                        </Button>
                      )}
                    </TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </CardContent>
        </Card>
      </div>
    </DashboardLayout>
  );
}
